rap: break-word;\n  white-space: pre-wrap;\n  word-break: normal;\n}\n\n.CodeMirror-linebackground {\n  position: absolute;\n  left: 0; right: 0; top: 0; bottom: 0;\n  z-index: 0;\n}\n\n.CodeMirror-linewidget {\n  position: relative;\n  z-index: 2;\n  padding: 0.1px; /* Force widget margins to stay inside of the container */\n}\n\n.CodeMirror-widget {}\n\n.CodeMirror-rtl pre { direction: rtl; }\n\n.CodeMirror-code {\n  outline: none;\n}\n\n/* Force content-box sizing for the elements where we expect it */\n.CodeMirror-scroll,\n.CodeMirror-sizer,\n.CodeMirror-gutter,\n.CodeMirror-gutters,\n.CodeMirror-linenumber {\n  -moz-box-sizing: content-box;\n  box-sizing: content-box;\n}\n\n.CodeMirror-measure {\n  position: absolute;\n  width: 100%;\n  height: 0;\n  overflow: hidden;\n  visibility: hidden;\n}\n\n.CodeMirror-cursor {\n  position: absolute;\n  pointer-events: none;\n}\n.CodeMirror-measure pre { position: static; }\n\ndiv.CodeMirror-cursors {\n  visibility: hidden;\n  position: relative;\n  z-index: 3;\n}\ndiv.CodeMirror-dragcursors {\n  visibility: visible;\n}\n\n.CodeMirror-focused div.CodeMirror-cursors {\n  visibility: visible;\n}\n\n.CodeMirror-selected { background: #d9d9d9; }\n.CodeMirror-focused .CodeMirror-selected { background: #d7d4f0; }\n.CodeMirror-crosshair { cursor: crosshair; }\n.CodeMirror-line::selection, .CodeMirror-line > span::selection, .CodeMirror-line > span > span::selection { background: #d7d4f0; }\n.CodeMirror-line::-moz-selection, .CodeMirror-line > span::-moz-selection, .CodeMirror-line > span > span::-moz-selection { background: #d7d4f0; }\n\n.cm-searching {\n  background-color: #ffa;\n  background-color: rgba(255, 255, 0, .4);\n}\n\n/* Used to force a border model for a node */\n.cm-force-border { padding-right: .1px; }\n\n@media print {\n  /* Hide the cursor when printing */\n  .CodeMirror div.CodeMirror-cursors {\n    visibility: hidden;\n  }\n}\n\n/* See issue #2901 */\n.cm-tab-wrap-hack:after { content: ''; }\n\n/* Help users use markselection to safely style text background */\nspan.CodeMirror-selectedtext { background: none; }\n","/* CSS font colors for translated ANSI escape sequences */\n\n.ansicolors(@name, @normal, @intense) {\n  .ansi-@{name}-fg { color: @normal; }\n  .ansi-@{name}-bg { background-color: @normal; }\n  .ansi-@{name}-intense-fg { color: @intense; }\n  .ansi-@{name}-intense-bg { background-color: @intense; }\n}\n\n/* The color values are a mix of\n   http://www.xcolors.net/dl/baskerville-ivorylight and\n   http://www.xcolors.net/dl/euphrasia */\n\n.ansicolors(black, #3E424D, #282C36);\n.ansicolors(red, #E75C58, #B22B31);\n.ansicolors(green, #00A250, #007427);\n.ansicolors(yellow, #DDB62B, #B27D12);\n.ansicolors(blue, #208FFB, #0065CA);\n.ansicolors(magenta, #D160C4, #A03196);\n.ansicolors(cyan, #60C6C8, #258F8F);\n.ansicolors(white, #C5C1B4, #A1A6B2);\n\n.ansi-default-inverse-fg { color: #FFFFFF; }\n.ansi-default-inverse-bg { background-color: #000000; }\n\n.ansi-bold { font-weight: bold; }\n.ansi-underline { text-decoration: underline; }\n","@_cell_padding_minus_border: @cell_padding - @cell_border_width;\n\n._selected_style(@border_color) {\n    position: absolute;\n    display: block;\n    top: -1 * @cell_border_width;\n    left: -1 * @cell_border_width;\n    width: @cell_left_border_width;\n    // height: 100% + (2 * @cell_border_width);\n    height: calc(~\"100% + \"(2 * @cell_border_width));\n    content: '';\n    background: @border_color;\n}\n\n\ndiv.cell {\n    .vbox();\n    .corner-all();\n    .border-box-sizing();\n    \n    border-width: @cell_border_width;\n    border-style: solid;\n    border-color: transparent;\n\n    width: 100%;\n    padding: @_cell_padding_minus_border;\n    /* This acts as a spacer between cells, that is outside the border */\n    margin: 0px;\n    outline: none;\n    \n    position: relative;\n    overflow: visible;\n\n    &:before {\n        ._selected_style(transparent);\n    }\n\n    &.jupyter-soft-selected {\n        border-left-color: @soft_select_color;\n        border-left-width: @cell_border_width;\n        padding-left: @cell_padding - @cell_border_width;\n        border-right-color: @soft_select_color;\n        border-right-width: @cell_border_width;\n        background: @soft_select_color;\n    \n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    &.selected, &.selected.jupyter-soft-selected {\n        border-color: @border_color;\n        \n        &:before {\n            ._selected_style(@selected_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    .edit_mode &.selected {\n        border-color: @edit_mode_border_color;\n        \n        &:before {\n            ._selected_style(@edit_mode_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n}\n\n\n.prompt {\n    /* This needs to be wide enough for 3 digit prompt numbers: In[100]: */\n    min-width: 14ex;\n\n    /* This padding is tuned to match the padding on the CodeMirror editor. */\n    padding: @code_padding;\n    margin: 0px;\n    font-family: @font-family-monospace;\n    text-align: right;\n\n    /* This has to match that of the the CodeMirror class line-height below */\n    line-height: @code_line_height;\n\n    /* Don't highlight prompt number selection */\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n\n    /* Use default cursor */\n    cursor: default;\n}\n\n@media (max-width: @screen-xs-min) {\n    // prompts are in the main column on small screens,\n    // so text should be left-aligned\n    .prompt {\n        text-align: left;\n    }\n}\n\ndiv.inner_cell {\n    min-width: 0;\n    .vbox();\n    .box-flex1();\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_area {\n    border: 1px solid @light_border_color;\n    .corner-all;\n    background: @cell_background;\n    line-height: @code_line_height;\n}\n\n/* This is needed so that empty prompt areas can collapse to zero height when there\n   is no content in the output_subarea and the prompt. The main purpose of this is\n   to make sure that empty JavaScript output_subareas have no height. */\ndiv.prompt:empty {\n    padding-top: 0;\n    padding-bottom: 0;\n}\n\ndiv.unrecognized_cell {\n    // from text_cell\n    padding: 5px @_cell_padding_minus_border 5px 0px;\n    .hbox();\n\n    .inner_cell {\n        .border-radius(@border-radius-base);\n        padding: @_cell_padding_minus_border;\n        font-weight: bold;\n        color: red;\n        border: @cell_border_width solid @light_border_color;\n        background: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 8.2%));\n        // remove decoration from link\n        a {\n            color: inherit;\n            text-decoration: none;\n\n            &:hover {\n                color: inherit;\n                text-decoration: none;\n            }\n        }\n    }\n}\n@media (max-width: @screen-xs-min) {\n    // remove prompt indentation on small screens\n    div.unrecognized_cell > div.prompt {\n        display: none;\n    }\n}\n","div.code_cell {\n    /* avoid page breaking on code cells when printing */\n    @media print {\n        page-break-inside: avoid;\n    }\n}\n\n/* any special styling for code cells that are currently running goes here */\ndiv.code_cell.running {\n}\n\ndiv.input {\n    page-break-inside: avoid;\n    .hbox();\n}\n\n@media (max-width: @screen-xs-min) {\n    // move prompts above code on small screens\n    div.input {\n        .vbox();\n    }\n}\n\ndiv.prompt_container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: flex-start;\n    text-align: right;\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_prompt {\n    color: @input_prompt_color;\n    border-top: 1px solid transparent;\n}\n\ndiv.run_this_cell {\n    display: none;\n    cursor: pointer;\n    color: #333;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    padding-left: 2ex;\n    padding-right: 2ex;\n    width: 1ex;\n}\n\n// set the prompt width\ndiv.prompt {\n    min-width: 15ex;\n}\n\n@media (-moz-touch-enabled: 1), (any-pointer: coarse) {\n    div.run_this_cell {\n        display: block;\n    }\n}\n\n// The styles related to div.highlight are for nbconvert HTML output only. This works\n// because the .highlight div isn't present in the live notebook. We could put this into\n// nbconvert, but it easily falls out of sync, can't use our less variables and doesn't\n// help the basic template when paired with our CSS.\n\ndiv.input_area > div.highlight {\n    margin: @code_padding;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n\ndiv.input_area > div.highlight > pre {\n    margin: 0px;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n","/* The following gets added to the <head> if it is detected that the user has a\n * monospace font with inconsistent normal/bold/italic height.  See\n * notebookmain.js.  Such fonts will have keywords vertically offset with\n * respect to the rest of the text.  The user should select a better font.\n * See: https://github.com/ipython/ipython/issues/1503\n *\n * .CodeMirror span {\n *      vertical-align: bottom;\n * }\n */\n\n.CodeMirror {\n    line-height: @code_line_height;  /* Changed from 1em to our global default */\n    font-size: @notebook_font_size;\n    height: auto;     /* Changed to auto to autogrow */\n    background: none;  /* Changed from white to allow our bg to show through */\n}\n\n.CodeMirror-scroll {\n    /*  The CodeMirror docs are a bit fuzzy on if overflow-y should be hidden or visible.*/\n    /*  We have found that if it is visible, vertical scrollbars appear with font size changes.*/\n    overflow-y: hidden;\n    overflow-x: auto;\n}\n\n.CodeMirror-lines {\n    /* In CM2, this used to be 0.4em, but in CM3 it went to 4px. We need the em value because */\n    /* we have set a different line-height and want this to scale with that. */\n    /* Note that this should set vertical padding only, since CodeMirror assumes\n       that horizontal padding will be set on CodeMirror pre */\n    padding: @code_padding 0;\n}\n\n.CodeMirror-linenumber {\n    // This is needed to fine tune the position of the line numbers because we use the 0.4em in @code_padding\n    // spacing in various places. Fine tuned to look right.\n    padding: 0 8px 0 4px;\n}\n\n.CodeMirror-gutters {\n    // This is needed because our cell has rounded corners, otherwise the gutter area square\n    // corner cuts into the rounded cell border.\n    border-bottom-left-radius: @border-radius-base;\n    border-top-left-radius: @border-radius-base;\n}\n\n.CodeMirror pre {\n    /* In CM3 this went to 4px from 0 in CM2. This sets horizontal padding only,\n    use .CodeMirror-lines for vertical */\n    padding: 0 @code_padding;\n    border: 0;\n    .border-radius(0)\n}\n\n.CodeMirror-cursor {\n    border-left: 1.4px solid black;\n}\n\n// When zoomed out 67% and 33% on a screen of 1440 width x 900 height\n@media screen and (min-width: 2138px) and (max-width: 4319px)  {\n    .CodeMirror-cursor {\n        border-left: 2px solid black;\n    }\n}\n\n// When zoomed out less than 33%\n@media screen and (min-width: 4320px)  {\n    .CodeMirror-cursor {\n        border-left: 4px solid black;\n    }\n}\n","/*\n\nOriginal style from softwaremaniacs.org (c) Ivan Sagalaev <Maniac@SoftwareManiacs.Org>\nAdapted from GitHub theme\n\n*/\n\n@import (reference) \"highlight-refs.less\";\n\n@highlight-base: #000;\n\n.highlight-base {\n  color: @highlight-base;\n}\n\n.highlight-variable {\n  .highlight-base();\n}\n\n.highlight-variable-2 {\n  // color: lighten(@highlight-base, 10%);\n  // [basthon] self is a variable-2 and must be visible\n  color: var(--self-color);\n}\n\n.highlight-variable-3 {\n  color: lighten(@highlight-base, 20%);\n}\n\n.highlight-string {\n  color: #ba2121;\n}\n\n//fix issue #4206, overwrite .highlight-comment class, from #408080 to #007979\n.highlight-comment {\n  color: #007979;\n  font-style: italic;\n}\n\n.highlight-number {\n  color: #080;\n}\n\n.highlight-atom {\n  color: #88f;\n}\n\n.highlight-keyword {\n  color: #008000;\n  font-weight: bold;\n}\n\n.highlight-builtin {\n  color: #008000;\n}\n\n.highlight-error {\n  color: #f00;\n}\n\n.highlight-operator {\n  color: #aa22ff;\n  font-weight: bold;\n}\n\n.highlight-meta {\n  color: #aa22ff;\n}\n\n/* previously not defined, copying from default codemirror */\n.highlight-def {\n  .cm-s-default.cm-def();\n}\n.highlight-punctuation {\n  .cm-s-default.cm-punctuation();\n}\n.highlight-property {\n  .cm-s-default.cm-property();\n}\n.highlight-string-2 {\n  .cm-s-default.cm-string-2();\n}\n.highlight-qualifier {\n  .cm-s-default.cm-qualifier();\n}\n.highlight-bracket {\n  .cm-s-default.cm-bracket();\n}\n.highlight-tag {\n  .cm-s-default.cm-tag();\n}\n.highlight-attribute {\n  .cm-s-default.cm-attribute();\n}\n.highlight-header {\n  .cm-s-default.cm-header();\n}\n.highlight-quote {\n  .cm-s-default.cm-quote();\n}\n.highlight-link {\n  .cm-s-default.cm-link();\n}\n\n/* apply the same style to codemirror */\n.cm-s-ipython span {\n  &.cm-keyword {\n    .highlight-keyword();\n  }\n  &.cm-atom {\n    .highlight-atom();\n  }\n  &.cm-number {\n    .highlight-number();\n  }\n  &.cm-def {\n    .highlight-def();\n  }\n  &.cm-variable {\n    .highlight-variable();\n  }\n  &.cm-punctuation {\n    .highlight-punctuation();\n  }\n  &.cm-property {\n    .highlight-property();\n  }\n  &.cm-operator {\n    .highlight-operator();\n  }\n  &.cm-variable-2 {\n    .highlight-variable-2();\n  }\n  &.cm-variable-3 {\n    .highlight-variable-3();\n  }\n  &.cm-comment {\n    .highlight-comment();\n  }\n  &.cm-string {\n  